/* App.JS */
.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(25px + 2vmin);
  color: white !important;
}

.App-text {
  font-size: calc(3px + 2vmin);
  text-align: left !important;
}

.App-link {
  color: #09d3ac;
  text-underline-position: unset;
}

.center {
  text-align: center;
}

.center-vert {
  margin-top: auto !important;
  margin-bottom: auto !important;
}

.lobby-size {
  font-size: 3rem !important;
}

.white-txt {
  color: #ffffff !important;
}

.whiteboard-size {
  width: 100% !important;
  height: 78vh !important;
}

.whiteboard-menu {
  display: flex;
  width: 100%;
}

.score-size {
  font-size: 1.5rem !important;
}

.header-wb {
  width: 100% !important;
  height: 10% !important;
  margin: 0 !important;
  padding: 0 !important;
}

.footer-wb {
  width: 100% !important;
  height: 10% !important;
  margin: 0 !important;
  padding: 0 !important;
}

.menu-bar {
  width: 100%;
  position: fixed; /* Set the navbar to fixed position */
  top: 1rem;
}

.dimmer-custom {
  margin: 0 !important;
  padding: 0 !important;
  border: 0 !important;
}
/* First.js */
